<template>
<div class="merchant-detail">
  <h1>商家详情</h1>
  <el-tabs v-model="activeName">
    <el-tab-pane label="基本信息" name="1">
      <el-form ref="myForm" :model="myForm" label-width="80px" class="form-group" :rules="rules">
        <el-form-item label="店铺名称" prop="name">
          <el-input v-model="myForm.name" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item label="店铺地址" prop="address">
          <el-input v-model="myForm.address" placeholder="请输入地址"></el-input>
        </el-form-item>
        <el-form-item label="店铺logo" prop="logo" title="商家logo">
          <el-upload
            class="logo-upload"
            :action="upload_action"
            :header="headers"
            :show-file-list="false"
            :on-success="onUploadSuccess"
            :before-upload="onBeforeUpload"
          >
            <img v-if="myForm.logo" :src="myForm.logo" class="logo" />
            <i v-else class="el-icon-plus logo-upload-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="店铺经度" prop="lon">
          <el-input v-model="myForm.lon" placeholder="请输入经度"></el-input>
        </el-form-item>
        <el-form-item label="店铺纬度" prop="lat">
          <el-input v-model="myForm.lat" placeholder="请输入纬度"></el-input>
        </el-form-item>
        <el-form-item label="店铺公告" prop="notice">
          <el-input v-model="myForm.notice" placeholder="请输入公告"></el-input>
        </el-form-item>
        <el-form-item label="起送价格" prop="up_send">
          <el-input v-model="myForm.up_send" placeholder="请输入起送价"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.prevent="onSubmit">提交</el-button>
        </el-form-item>
      </el-form>
    </el-tab-pane>
    <el-tab-pane label="商品管理" name="2">
      <ul class="category-list">
        <li class="category-group" v-for="(category,index) in categories" :key="category.id+category.name">
          <div class="category-info category-box">
            <span class="category-title">{{category.name}}</span>
            <div>
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="onEditCategory(category)">编辑</el-button>
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="onDeleteCategory(category,index)">删除</el-button>
            </div>
          </div>
          <ul class="goods-list">
            <li class="goods-group" v-for="(goods,index) in category.goods_list" :key="goods.id+goods.name">
              <span class="goods-title">{{goods.name}}</span>
              <div class="ops-group">
                <span class="edit" @click="onEditGoods(goods)">编辑</span>
                <span class="delete" @click="onDeleteGoods(goods.id,index)">删除</span>
              </div>
            </li>
            <li class="add-goods" style="font-weight:800" @click="onAddGoods(category)">+新增商品</li>
          </ul>
        </li>
        <li class="category-group">
          <div class="category-box add-category" @click="OnShowDialog">+新增分类</div>
        </li>
      </ul>
    </el-tab-pane>
  </el-tabs>
  <el-dialog title="新增/编辑分类" :visible.sync="categoryDialogVisible">
    <el-form :model="categoryForm">
      <el-form-item label="分类名" label-width="80px">
        <el-input v-model="categoryForm.name" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="Quxiao">取 消</el-button>
      <el-button type="primary" @click="OnaddCategory">确 定</el-button>
    </div>
  </el-dialog>

  <el-dialog title="新增/编辑商品" :visible.sync="goodsDialogVisible" width="40%">
    <el-form :model="goodsForm" :rules="goodsRules" ref="goodsForm">
      <el-form-item label="商品名称" label-width="80px" prop="name">
        <el-input v-model="goodsForm.name" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="商品图片" label-width="80px" prop="picture">
        <el-upload
          class="logo-upload"
          :action="upload_action"
          :headers="headers"
          :show-file-list="false"
          :on-success="onGoodsPictureUploadSuccess"
          :before-upload="onBeforeUpload"
        >
          <img v-if="goodsForm.picture" :src="goodsForm.picture" class="logo" />
          <i v-else class="el-icon-plus logo-upload-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="商品价格" label-width="80px" prop="price">
        <el-input v-model="goodsForm.price" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="商品介绍" label-width="80px" prop="intro">
        <el-input v-model="goodsForm.intro" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="Quxiao2">取 消</el-button>
      <el-button type="primary" @click="onSubmitGoods">确 定</el-button>
    </div>
  </el-dialog>
</div>
</template>

<script type="text/ecmascript-6">
import {Tabs,TabPane,Form,FormItem,Input,Upload,Button,Dialog} from "element-ui"
// import axios from "axios"
export default {
  name: "MerchantDetail",
  data() {
    return {
      activeName: "1",
      upload_action: "http://127.0.0.1:8000/cms/upload",
      merchant_id: null,
      myForm: {
        name: "",
        logo: "",
        address: "",
        notice: "",
        up_send: "0",
        lon: "",
        lat: ""
      },
      rules: {
        name: [{ required: true, message: "请输入商家名称！", trigger: "blur" }],
        address: [{ required: true, message: "请输入地址！", trigger: "blur" }],
        logo: [{ required: true, message: "请上传图片！", trigger: "blur" }],
        lon: [{ required: true, message: "请输入经度！", trigger: "blur" }],
        lat: [{ required: true, message: "请输入纬度！", trigger: "blur" }]
      },
      headers: {
        "Authorization": "JWT " + this.$auth.token
      },
      categories: [],
      categoryDialogVisible: false,
      categoryForm: {
        name: null,
        id: null,
        merchant_id: null
      },
      goodsDialogVisible: false,
      goodsForm: {
        id: '',
        name: '',
        picture: '',
        price: '',
        intro: "",
        category: ""
      },
      goodsRules: {
        name: [{ required: true, message: "请输入商品名称！", trigger: "blur" }],
        intro: [{ required: true, message: "请输入商品介绍！", trigger: "blur" }],
        picture: [{ required: true, message: "请上传图片！", trigger: "blur" }],
        price: [{ required: true, message: "请输入价格！", trigger: "blur" }]
      },
    
    }
  },
  components: {
    [Tabs.name]: Tabs,
    [TabPane.name]: TabPane,
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Input.name]: Input,
    [Upload.name]: Upload,
    [Button.name]: Button,
    [Dialog.name]: Dialog
  },
  mounted(){
    const merchant_id = this.$route.query.id
    if(merchant_id){
      this.merchant_id=merchant_id
      this.$loading.show()
      this.$http.getMerchant(merchant_id).then(res =>{ 
        this.myForm.name = res.data.name
        this.myForm.logo = res.data.logo
        this.myForm.address = res.data.address
        this.myForm.notice = res.data.notice
        this.myForm.up_send = res.data.up_send
        this.myForm.lon = res.data.lon
        this.myForm.lat = res.data.lat
        this.$loading.hide()
      }).catch(err =>{
        console.log(err)
        this.$loading.hide()
      })
      this.$http.getCategories(merchant_id).then(res =>{
        console.log(res)
        this.categories = res.data
      }).catch(err =>{
        console.log(err)
      })
    }


  },
  methods: {
    onUploadSuccess(res,file){
      console.log(res)
      this.myForm.logo=res.picture    
       console.log(file)
    },
     onBeforeUpload(file){
      const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          alert('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          alert('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
    },
    onSubmit(){
      if(this.merchant_id){
        this.$http.updateMerchant(this.merchant_id,this.myForm).then(res =>{
          console.log(res)
          this.$router.push({'name':'merchant'})
        }).catch(err =>{
          console.log(err)
        })
      }else{
        this.$http.addMerchant(this.myForm).then(res =>{
          console.log(res)
         }).catch(err =>{
          console.log(err)
       })
      }
    
      
    },
     onGoodsPictureUploadSuccess(res,file){
      this.goodsForm.picture = res.picture
      console.log(file)
    },
      Quxiao(){
        this.categoryDialogVisible = false
      },
    OnShowDialog(){
      this.categoryDialogVisible = true
    },
    OnaddCategory(){
      this.categoryForm.merchant_id = this.merchant_id
      if (this.categoryForm.id){
        this.$http.updateCategory(this.categoryForm.id,this.categoryForm).then(res =>{
          console.log(res)
          this.categoryDialogVisible = false
          this.$message.success()
          for (let category of this.categories){
            if (this.categoryForm.id === category.id){
              category.name = this.categoryForm.name
              break
            }
          }
          this.categoryForm.id = null
          this.categoryForm.name = null
          this.categoryForm.merchant_id = null
        
        }).catch(err =>{
          this.$message.error(err)
        })
      }else{
        this.$http.addCategory(this.categoryForm).then(res =>{
          this.categoryDialogVisible = false
         
          const category = res.data
          this.categories.push(category)
          this.$message.success("添加成功")
        
        }).catch(err =>{
          console.log(err)
        })
      }
     
    },
    onEditCategory(category){
      console.log("category = ",category)
      this.categoryDialogVisible = true
      this.categoryForm.id = category.id
      this.categoryForm.name = category.name
      this.categoryForm.merchant_id = category.merchant_id
      
    },
    onDeleteCategory(category,index){
      console.log(category)
      if (category.goods_list.length>0){
          this.$message.info("当前分类下还有商品,无法删除")
          return
        }
      this.$http.deleteCategory(category.id).then(res =>{
        console.log(res)
        this.$message.success("删除成功")
        this.categories.splice(index,1)
      }).catch(err =>{
        this.$message.error(err)
      })
    },
    onAddGoods(category){
      console.log(category)
      this.goodsDialogVisible = true
      this.goodsForm.category = category.id
    },
    Quxiao2(){
      this.goodsDialogVisible = false
    },
    onSubmitGoods(){
      if(this.goodsForm.id){
        console.log(this.goodsForm)
        this.$http.editGoods(this.goodsForm.id,this.goodsForm).then(res =>{
          this.$message.success("更改成功！")
          this.goodsDialogVisible = false
          for (let category of this.categories){
            for (let goods of category.goods_list){
              if (goods.id == this.goodsForm.id){
                goods =JSON.parse(JSON.stringify(this.goodsForm))
              }
            }
          }
          console.log(res)
        }).catch(err =>{
          this.$message.error(err)
        })
      }else{
        this.$http.addGoods(this.goodsForm).then(res =>{
           this.$message.success("添加成功！")
           this.goodsDialogVisible = false
           console.log(res)
          for(let category of this.categories){
            if(res.data.category == category.id){
              console.log("category=",category)
              console.log("form=",this.goodsForm)
              category.goods_list.push(JSON.parse(JSON.stringify(this.goodsForm)))
            }
           }
          this.initGoodsform()
        }).catch(err =>{
            this.$message.error(err)
        })

        
      }
    },
   
    onEditGoods(goods){
       this.goodsDialogVisible = true
       this.goodsForm = goods
    },
    initGoodsform(){
      this.goodsForm.id = ''
      this.goodsForm.name = ''
      this.goodsForm.picture = ''
      this.goodsForm.price = ''
      this.goodsForm.intro = ''
      this.goodsForm.category = ''
   
    },
    onDeleteGoods(id,index){
      this.$http.deleteGoods(id).then(res =>{
        console.log(res)
        this.$message.success("删除成功")
        for (let category of this.categories){
          for (let good of category.goods_list){
            if(id == good.id){
              category.goods_list.splice(index,1)
            }
          }
        }
      }).catch(err =>{
        console.log(err)
      })
    }
  }
}
</script>

<style scoped lang='scss'>
.form-group{
  padding-top: 20px;
  width: 460px;
}

.logo-upload{
  .logo {
    width: 178px;
    height: 133px;
    display: block;
  }
  .logo-upload-icon{
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 133px;
    line-height: 133px;
    text-align: center;
  }
}

.category-list{
  width: 800px;
  .category-group{
    .category-box{
      padding: 10px;
      box-sizing: border-box;
    }
    .add-category{
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
    }
    .category-info{
      background-color: rgb(217, 236, 255);
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .goods-list{
      .goods-group{
        font-size: 14px;
        display: flex;
        justify-content: space-between;
        padding: 10px;
        margin: 10px 0;
        background-color: #ddd;
        &:hover{
          background-color: #ccc;
        }
        &:hover .ops-group{
          display: block;
        }
        .ops-group{
          display:none;
          font-size: 12px;
          justify-content: space-between;
          span{
            cursor: pointer;
          }
          .edit{
            margin-right: 10px;
          }
        }
      }
      .add-goods{
        background-color: #ddd;
        text-align: center;
        padding: 10px;
        cursor: pointer;
        margin-bottom: 20px;
      }
    }
  }
}
</style>

<style scoped>
.logo-upload >>> .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.logo-upload >>> .el-upload:hover {
  border-color: #409eff;
}
</style>