<template>
<div class="">
  <h1>商家列表</h1>
  <div class="list-group">
    <el-row :gutter="20">
    
       <el-col :span="6" class="col-group" v-for="merchant in merchants" :key="merchant.id">
         <router-link   :to="'/merchant/detail/?id='+merchant.id">
           <el-card class="card-group">
             <img :src="merchant.logo" class="thumbnail">
             <div class="title">{{merchant.name}}</div>
           </el-card>
         </router-link>
       </el-col>
         <el-col :span="6" class="col-group">
        <router-link  to="/merchant/detail">
          <el-card class="card-group add-card">+</el-card>
        </router-link>
      </el-col>
    </el-row>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="total"
      :page-size="11"
      :current-page="page"
      @current-change="onCurrentChange"
      class="pagination"
      >
    </el-pagination>
  </div>
</div>
</template>

<script type="text/ecmascript-6">
import {Row,Col,Card,Pagination} from "element-ui"


export default {
  name: "Merchant",
  data() {
    return {
      merchants: [],
      total: 0,
      page: 1
    }
  },
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [Card.name]: Card,
    [Pagination.name]: Pagination
  },
  mounted(){
    console.log(this.$http)
   this.GetData(1)
  },
  methods: {
    onCurrentChange(page){
      this.page=page
      this.GetData(page)
    },
    GetData(page){
      this.$http.getMerchants(page).then(res=>{    
      const data = res.data.results
      this.total=res.data.count
      this.merchants = data  
    }).catch(err =>{
      console.log(err)
    })
    },
    
  }
}
</script>

<style scoped lang='scss'>
.list-group{  
  width: 800px;
  .col-group{
    margin-top: 20px;
    cursor: pointer;
    .card-group{
      height: 180px;
      width: 100%;
      .thumbnail{
        width: 100%;
      }
      .title{
        padding-top: 10px;
      }
    }
    .add-card{
      font: bolder 100px '微软雅黑';
      display: flex;
      text-align: center;
      justify-content: center;
      color: #BBBEC7;
      &:hover{
        box-shadow: 0px 0 1px rgba(0, 0, 0, 0.4);
      }
    }
  }
  .pagination{
    text-align: right;
    padding-top: 20px;
  }
}
</style>