<template>
  <div class="login-container">
    <el-row>
      <el-col :span="4" :offset="10">
        <h1 class='page-title'>知了管理系统</h1>
        <el-form ref="loginForm" :model="loginForm" label-width="80px" class='login-form' :rules="rules">
          <el-form-item label="手机" prop="username">
            <el-input v-model="loginForm.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="loginForm.password" type="password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="Submit_it">登录</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {Button,Row,Col,Form,Input,FormItem} from "element-ui"
export default {
  name: 'Login',
  data(){
    var ValidateTel = (valid,value,callback)=>{
      if (value.match(/1[23456789]\d{9}/)){
        callback()
      }else{
        callback(new Error("请输入正确的手机号码"))
      }
    }
    return {
      loginForm: {
        username: "",
        password: ""
      },
      rules:{
        username:[
          {required:true,message:"请输入手机号码",trigger:'blur'},
          {validator:ValidateTel,trigger:'blur'}
        ],
        password:[
           {required:true,message:"请输入密码",trigger:'blur'},
           {max:12,min:6,message:"密码长度应在6-12位之间!",trigger:'blur'}
        ]
      }
    
    }
  },
  components: {
    [Button.name]: Button,
    [Row.name]: Row,
    [Col.name]: Col,
    [Form.name]: Form,
    [Input.name]: Input,
    [FormItem.name]: FormItem
  },
  methods: {
    Submit_it(){
      this.$refs['loginForm'].validate(valid =>{
        if(!valid){
            console.log("验证未同构") 
            return 
        }else{
            var params = {
              username:this.loginForm.username,
              password:this.loginForm.password
            }
            this.$loading.show()
            this.$http.login(params).then(res =>{
              const user = res.data.user
              const token = res.data.jwt
              this.$auth.SetTokenUser(token,user)
              this.$router.push("/")
              this.$loading.hide()
            }).catch(err=>{
              console.log(err)
              this.$loading.hide()
            })
        }
      }
     )
       
    
    }
    }
  
}
</script>

<style lang="scss" scoped>
body{
  background-color: #eee;
}
.login-container{
  padding-top: 80px;
  .page-title{
    text-align: center;
  }
  .login-form{
    padding-top: 40px;
  }
}
</style>
